body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.form {
  max-width: 800px;
  background: #fafafa;
  margin: auto;
  padding: 0 10px;
  background: #fafafa;
  border-radius: 8px;
  font-family: Georgia, "Times New Roman", Times, serif;
  position: relative;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
.Partner{
  padding-bottom: 25px;
  margin-top: -5px;
}
legend:first-child{
  padding-bottom: 15px!important;
}
legend {
  font-size: 1.4em;
  margin: 20px 0 0;
  cursor: context-menu;
}
.car-component{
  padding-bottom: 25px;
  margin-bottom: 5px;
  margin-top: 20px;

  position: relative;
  z-index: 9;
  
}
label{
  text-align: left!important;
}
.icon {
  background: #0d47a1;
  color: #fff;
  height: 50px;
  width: 50px;
  display: inline-block;
  font-size: 0.8em;
  margin-right: 4px;
  line-height: 60px;
  text-align: center;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
  border-radius: 50%;
}
.radio-container {
  width: 90%;
  margin-left: 20px;
  margin-right: 20px;
  padding-top: 20px;
  margin-bottom: 8px;
  margin: auto;
  text-align: left;
}
.radio-title{
  height: 22px!important;
}
.MuiRadio-colorPrimary.Mui-checked {
  color: #0d47a1 !important;
}
.MuiButton-containedPrimary {
  background-color: #0d47a1 !important;
  font-weight: bold;
}
.select-container {
  margin: auto !important;
  max-width: 87% !important;
  text-align: left;
}
.custom-container {
  margin: 8px;
  width: calc(100%);
  margin: auto;
}
.custom-container div {
  width: 100%;
}
.Prices label {
  text-align: left;
  padding-bottom: 5px;
  white-space: nowrap;
}
.MuiFab-root{
  width: 50px!important;
  height: 50px!important;
}
.addCarFormContainer {
  position: absolute !important;
  right: 10px;
  text-align: right;
  margin-top: -70px !important;
  /* color: #0d47a1; */
}
.removeButtonContainer{
  position: absolute !important;
  right: 10px;
  text-align: right;
  margin-top: -60px !important;
}
.Prices .addCarFormContainer{
  margin-top: -56px !important;
}
.addCarFormContainer span,
.removeButtonContainer span {
  cursor: pointer;
}
.removeButtonContainer {
  position: absolute !important;
  right: 10px;
  text-align: right;
  margin-top: 10px;
}
.car-inputs:not(:first-child) {
  margin-top: 100px;
}
.priceFormContainer:not(:first-child){
  margin-top: 100px;
}
.dropZone-wrapper {
  width: 100%;
  
}
.dropZone{
  width: 200px !important; 
  margin: auto;
  margin-top: 20px;
  padding: 15px 20px;
  padding-top: 5px;
  background-color: rgba(0, 66, 112, 0.1);
  border: 2px dashed #0d47a1;
  color: #0d47a1;
  font-weight: 500;
  
}

.absoluteCounter{
  position: absolute;
  top: -60px;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  bottom: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 140px;
  z-index: 0;

  color: rgba(128, 128, 128, 0.178);

  font-family: "Roboto", "Helvetica", "Arial", sans-serif;

}
.carFormContainer{
  position: relative;
}
.addCarFormContainer, .removeButtonContainer{
  z-index: 9;
}

.addCarFormContainer,
.removeButtonContainer {
  border-radius: 50%;
  padding: 5px 10px;
  color: white;
  float: right;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  font-size: 20px;
}
.addCarFormContainer {
  background-color: #0d47a1!important;
}
.removeButtonContainer {
  background-color: #f44336;
  padding: 5px 13px;
}
.imgContainer img{
  object-fit: cover;
}
.imgContainer:hover .removeImage{
  display: flex;
}
.removeImage{
  display: none;
  background-color: rgba(13, 71, 161, 1);
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 10px;
  left: 0px;
  z-index: 999;
  right: 13px;
  cursor: pointer;
  color: #e1e8ec
}
.invalid *{
  color: red !important;

}
.w90Ma > div{
  width: 90% !important;
  margin: auto;

}
.selectContainer > div{
  margin-top: -4px;
}
.send {
  margin: 20px auto !important;
}
#pricesContainer{
  width: 100%!important;
}
.Prices{
  position: relative;
}
.priceCounter{
  top: 0px;
  font-size: 150px;

  align-items: flex-start;
}

/* .car-component{
  min-height: 500px;
  background: #0d47a1;
} */
@media only screen and (max-width: 735px) {
  .Prices label {
    white-space: unset!important;
  }
  .imgContainer .removeImage{
    display: flex;
  }
  .removeImage{
    display: flex;
    background-color: rgba(255, 255, 255, 0.1);
    width: 100px;
    height: 100px;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 10px;
    left: 0px;
    z-index: 999;
    right: 13px;
    cursor: pointer;
    color: #0d47a1
  }
}
@media only screen and (max-width: 735px) {
  .Prices label {
    white-space: unset!important;
  }
}
@media only screen and (max-width: 625px) {
  .MuiInputLabel-formControl,
  .radio-container {
    font-size: 11px !important;
    text-align: center;
  }
  ::placeholder {
    font-size: 11px;
  }
}
@media only screen and (max-width: 350px) {
  .MuiFormControlLabel-root {
    width: 100%;
  }
}
